.App {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  background-color: #282c34;
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
}
.Buttons {
  display: flex;
  width: 50%;
  padding: 20px;
  justify-content: space-between;
}
.Button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 50px;
  cursor: pointer;
  border: solid 1px black;
  line-height: 50px;
  background-color: white;
  padding: 3px;
  &.ControlButton {
    width: 50px;
    height: 25px;
  }
}

.Control {
  font-size: 20px;
  background-color: red;
  width: 100%;
  height: 100%;
  align-items: center;
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 100px;
  .Input {
    height: 50px;
    width: 50%;
    font-size: 18px;
  }
}

.Box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Display {
  display: flex;
  padding: 5px;
  width: 20%;
  height: 80px;
  font-size: 18px;
  border: 3px black;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  &.On {
    background-color: greenyellow;
    color: black;
  }
  &.Off {
    background-color: darkolivegreen;
    color: darkgrey;
  }
}

.Display-amount {
  font-size: 50px;
}

@keyframes fill {

  to {
    background-size: 100% 0;
  }
}

.Cover {
  width: 100%;
  height: 20px;
  background-color: black;
}

.cnh_holding .Button {
  background: linear-gradient(white, white) rgb(255, 215, 235) no-repeat 0 0;
  mix-blend-mode: multiply;
  background-size: 100% 100%;
  -webkit-animation: fill 2s forwards;
  animation: fill 2s forwards;
}


